import NO from '@/components/_nodata'
export default {
    data() {
        return {
            
        }
    },
    components:{
        'no-data': NO
    },
    beforeCreate(){
        this.$store.dispatch('binding')
    },
    computed: {
        binding(){
            return this.$store.state.binding
        },
    },
    created(){
        
    },
    methods: {

    },
    filters: {
        fnName: function(value) {
            return value < 10 ? '0'+value : value
        }
    }
}



